export default {
	props: {
		item: Object,
		index: Number
	},
	methods: {
		// 打开个人空间
		openSpace() {

		},
		// 关注
		follow() {
			console.log('点击了关注!')
			// 通知父组件触发follow事件
			this.$emit('follow', this.index)
		},
		// 进入详情页
		openDetail() {

		},
		// 顶踩操作
		doSupport(type) {
			// 通知父组件触发了doSupport事件
			this.$emit('doSupport', {
				type,
				index: this.index
			})
		}
	}
}
